@charset "UTF-8";
@import "../settings";
@import "../mixin";
*{
  margin: 0;
  padding: 0;
}
body{
  font: $f14/ 1.5 tahoma, arial, Microsoft YaHei, sans-serif;
  background:$bg;
}
body{
  min-width: 1200px;
  font-size: $f14;
}
table{
  border-collapse: collapse;
}
a{
  text-decoration: none;
  transition:all .25s ease 0s;
}
li{
  list-style: none;
}
img{
  border: none;
}
h1,h2,h3,h4,h5,h6{
  font-size: 100%;
  font-weight:100;
}
.hide{
  display: none;
}
.fl{
  float: left;
}
.fr{
  float: right
}
.overflow{
  overflow: hidden;
}
/**/
.center{
  width: 1200px;
  margin:0 auto;
}
/**/
.mask{
  @include mask-five;
}
.maskes{
  @include mask-six
}
.opacity{
  @include mask-five;
  &:hover{
    @include mask-eight;
  }
}
.no-photo{
  @include no-photo;
}
.no-more{
  width: 100%;
  line-height:60px;
  color:$gray;
}
.no-data{
  width: 100%!important;
  line-height: 150px;
  text-align: center;
  color:red!important;
  background:none!important;
}
.loading{
  display: block;
  padding:100px 0;
  width: 100%!important;
  text-align: center;
  height: 30px;
  line-height:30px;
  background:none!important;
  color:$gray;
  font-size: $f16;
  &-ic{
    vertical-align: middle;
    display:inline-block;
    width: 30px;
    height: 30px;
    margin-right:10px;
    @include loading;
  }
}
.error{
  text-align: center;
  line-height: 100px;
  color:red;
}
//
.p-scale{
  text-align: center;
  img{
    height: 100%;
  }
  img{
    transition:all .25s ease 0s;
  }
  &:hover{
    img{
      transform:scale(1.1);
    }
  }
}
.p-scale1{
  text-align: center;
  img{
    width: 100%;
  }
  img{
    transition:all .25s ease 0s;
  }
  &:hover{
    img{
      transform:scale(1.1);
    }
  }
}


/**/
::-webkit-scrollbar{
  height: 6px;
  width: 6px;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
::-webkit-scrollbar-button{
  display: none
}
::-webkit-scrollbar-thumb{
  width: 6px;
  min-height: 15px;
  background: #b6c0cf !important;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  -webkit-border-radius: 2px;
  //border-radius: 6px;
}
.txt-c{
  text-align: center;
}
.clearfix:after { visibility: hidden; display: block; font-size: 0; content: "."; clear: both; height: 0; }
* html .clearfix { zoom: 1; }
*:first-child + html .clearfix { zoom: 1; }

//
.video-box1 {
  width: 1010px;
  height: 707px;
  overflow: hidden;
  position: fixed;
  z-index: 10002;
  left: 50%;
  margin-left: -505px;
  top: 50%;
  margin-top: -353px;
  filter: alpha(opacity=0);
  opacity: 0;
  -webkit-opacity: 0;
  -moz-opacity: 0;
}

.video-cont {
  position: relative;
  width: 980px;
  height: 595px;
  padding: 15px;
  background-color: rgba(255, 255, 255, 0.6);
  filter: progid:DXImageTransform.Microsoft.gradient(gradientType=0, startColorstr=#99ffffff, endColorstr=#99ffffff);
  -webkit-border-radius: 5px;
  border-radius: 5px;
}

.video-tit {
  position: relative;
  width: 100%;
  color: #000;
  text-align: center;
  filter: progid:DXImageTransform.Microsoft.gradient(gradientType=0, startColorstr=#99ffffff, endColorstr=#99ffffff);
  -webkit-border-radius: 5px;
  border-radius: 5px;
}

.video-tit p {
  position: absolute;
  right: 20px;
  top: 9px;
}

.vpro {
  width: 100%;
  height: 5px;
  position: absolute;
  margin-top: -2.5px;
  top: 50%;
  background: #000;
  left: -100%;
}
/**/
.opa80 {
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 10000;
  background: url(../../image/opa80.png) repeat;
}

.pop-remove {
  z-index: 10001;
  cursor: pointer;
}

.pop-close {
  display: block;
  position: fixed;
  right: 0;
  top: 0;
  width: 72px;
  height: 72px;
  background: url(../../image/tc_close.png) no-repeat;
  z-index: 10001;
}

.pop-close:hover {
  background: url(../../image/tc_closeH.png) no-repeat center;
}

.pop-btn {
  display: block;
  position: fixed;
  width: 70px;
  height: 150px;
  top: 50%;
  margin-top: -75px;
  z-index: 10002;
}

.pop-prev {
  left: 0;
  background: url(../../image/tc_left.png) no-repeat center;
}

.pop-prev:hover {
  background: url(../../image/tc_leftH.png) no-repeat center;
}

.pop-next {
  right: 0;
  background: url(../../image/tc_right.png) no-repeat center;
}

.pop-next:hover {
  background: url(../../image/tc_rightH.png) no-repeat center;
}

.pop-box {
  display: inline-block;
  position: fixed;
  z-index: 10002;
  left: 0;
  padding: 15px;
  background-color: rgba(255, 255, 255, 0.6);
  filter: progid:DXImageTransform.Microsoft.gradient(gradientType=0, startColorstr=#99ffffff, endColorstr=#99ffffff);
  -webkit-border-radius: 8px;
  border-radius: 8px;
}

.pop-tit {
  position: absolute;
  width: 100%;
  text-align: center;
  height: 32px;
  bottom: 35px;
  left: 0;
}

.pop-tit > span {
  display: inline-block;
  vertical-align: middle;
  *zoom: 1;
  *display: inline;
  *vertical-align: middle;
  *margin-top: auto;
  height: 32px;
  padding: 0 20px;
  font: normal 14px/32px "microsoft yahei";
  color: #fff;
  background-color: rgba(0, 0, 0, 0.6);
  filter: progid:DXImageTransform.Microsoft.gradient(gradientType=0, startColorstr=#99000000, endColorstr=#99000000);
  -webkit-border-radius: 32px;
  border-radius: 32px;
}

.webDa:hover {
  cursor: pointer
}
