@charset "UTF-8";
@import "../settings.scss";
.bg-f{
    background: #fff;
}
.g-seach-header{
    position:fixed;
    left:0;
    top:0;
    width:100%;
    height:66px;
    z-index:1;
    background:$themeColor;

    .s-logo{
        float:left;
        display:block;
        margin:12px 20px 0 20px;
        width:274px;
        height:30px;
    }
    .h-search{
        position:relative;
        float:left;
        height:36px;
        margin-top:17px;
        font:12px/36px "Microsoft YaHei";
        width:418px;
        background:#fff;
        border-radius:3px;
        color:#666;
        input{
            width:358px;
            padding:0 20px;
            color:#666;
        }
        .daq-icon{
            position:absolute;
            height:18px;
            width:18px;
            line-height:18px;
            right:12px;
            top:10px;
            font-size:18px;
            color:#969696;
            &:hover{
                color:$themeColor;
            }
        }
    }
    .login-right{
        float:right;
        margin-right:20px;
    }
    .unlogin-sta{
        &.none{
            display:none;
        }
        line-height:66px;
        position:relative;
        a{
            margin-left:25px;
            font-size:14px;
            color:#fff;
            &:hover{
                text-decoration: underline;
            }
        }
        i{
            position:absolute;
            left:50%;
            top:25px;
            width:1px;
            height:16px;
            background:#fff;
            margin:0 12px;
        }
    }
    .login-sta{
        line-height:66px;
        color:#fff;
        .user{
            position:relative;
            display: inline-block;
            *zoom:1;
            *display:inline;
            width:83px;
            margin-left:15px;
            padding-left:15px;
            font-size:14px;
            &:hover{
                .daq-icon{
                    transform: scale(.75) rotate(180deg);
                }
                ul{
                    display:block;
                }
            }
            .daq-icon{
                display:inline-block;
                color:#fff;
                font-size:12px;
                transform:scale(.75);
                transition: all .3s;
            }
            span{
                cursor: pointer;
            }
            ul{
                display:none;
                position:absolute;
                top:56px;
                left:0;
                width:118px;
                padding: 0 0 10px 0;
                font-size:12px;
                color:#fff;
                background:$themeColor;
                li{
                    height:30px;
                    line-height:30px;
                    padding-left:15px;
                    a{
                        color:#fff;
                        &:hover{
                            text-decoration: underline;
                        }
                    }
                }
            }
        }
    }
}
.g-search-wrapper{
    padding:66px 0 0 76px;
    background:#f5f5f5;
    .g-types{
        padding-top:7px;
    }
    .g-type{
        float:left;
        font:14px/42px "Microsoft YaHei";
        color:#666;
        padding:0 15px;
        border-bottom:3px solid #f5f5f5;
        text-align: center;
        cursor: pointer;
        &:hover,
        &.active{
            color:$themeColor;
            border-color:$themeColor;
        }
    }
}

.g-r-wrapper{
    min-height:630px;
    background:#fff;
    padding-left:90px;
    .page{
        padding-bottom:60px;
    }
    .g-r-container{
        width:870px;
    }
    .s-result{
        font:12px/68px "Microsoft YaHei";
        color:#999;
    }
    .r-list{
        padding:24px 0;
        .r-list-tit{
            font:bold 16px/36px "Microsoft YaHei";
            color:#333;
            border-bottom:1px solid #e8e8e8;
        }
        .r-more-btn{
            float: right;
            padding-right:21px;
            font-size:12px;
            color:$themeColor;
            background:url(../../image/g-s-r-arrow.png) no-repeat right center;
            &:hover{
                text-decoration: underline;
            }
        }
    }
    .tit{
        font:16px/24px "Microsoft YaHei";
        color:#444;
        a{
            color:#444;
            &:hover{
                color:$themeColor;
                b{
                    color:$themeColor;
                }
            }
        }
    }
    .summary{
        font:12px/20px "Microsoft YaHei";
        color:#888;
    }
    .r-news-list{
        .tit{
            padding:6px 0;
        }
        dd{
            padding-top:23px;
        }
    }
}
.r-secnie-list{
    .scenicp-list{
        width:100%;
        .tr-header{
            padding: 0 77px;
            width:715px;
            height:50px;
            background-color: #fff8f3;
            border-bottom: 1px solid #eee;
            div{
                float:left;
                text-align: center;
                font:14px/50px "Microsoft YaHei";
                color:#999;
            }
            .td1{
                text-align: left;
                width:230px
            }
            .td2{
                width:100px
            }
            .td3{
                width:200px
            }
        }
        .tr-list{
            padding: 0 77px;
            width:715px;
            height:92px;
            border-bottom: 1px solid #eee;
            div{
                float:left;
                text-align: center;
                font:14px/92px "Microsoft YaHei";
                color:#999;
            }
            .td1{
                text-align: left;
                width:230px;
                .mp-type{
                    padding-top: 14px;
                    font:18px/30px "Microsoft YaHei";
                    color:#666;
                }
                .font{
                    font:14px/30px "Microsoft YaHei";
                    color:#999;
                }
            }
            .td2{
                width:100px
            }
            .td3{
                color:#ff6633;
                width:200px;
                font:30px/92px "Microsoft YaHei";
                span{
                    font:14px/92px "Microsoft YaHei";
                    color:#ff6633;
                }
            }
            .td4{
                width:175px;
                a{
                    margin: 31px auto;
                    display:block;
                    width:102px;
                    height:30px;
                    font:16px/30px "Microsoft YaHei";
                    text-align: center;
                    color:white;
                    background-color: #ff7800;
                    border-radius: 4px;
                }
            }
        }
    }
    .card{
        position:relative;
        padding:30px 0;
        &:hover{
            img{
                transform: scale(1.2);
            }
        }
    }
    .left{
        display:block;
        float:left;
        overflow:hidden;
        width:180px;
        height:136px;
        img{
            margin:0 auto;
            display:block;
            height:100%;
            width:auto;
            transition: all .3s;
        }
    }
    .right{
        float:left;
        width:675px;
        padding-left:15px;
    }
    .tit{
        padding:6px 0 10px 0;
    }
    .s-r-l{
        width:60px;
    }
    .s-r-r{
        width:615px;
    }
    .price{
        position:absolute;
        right:0;
        top:32px;
        color:$themeBuyColor;
        font:24px/24px "Arial Regula";
        i{
            font-size:12px;
            font-style:normal;
        }
    }
}
.s-r-row{
    .s-r-l,
    .s-r-r{
        float:left;
        font:12px/24px "Microsoft YaHei";
    }
    .s-r-l{
        color:#999;
    }
    .s-r-r{
        color:#666;
    }
}

.r-hotel-list{
    overflow:hidden;
    dd{
        width:942px;
    }
    .card{
        float:left;
        width:422px;
        padding:30px 0;
        margin-right:36px;
        &:hover{
            img{
                transform: scale(1.2);
            }
        }
    }
    .left,
    .right{
        float:left;
    }
    .left{
        display:block;
        float:left;
        overflow:hidden;
        width:180px;
        height:136px;
        img{
            margin:0 auto;
            display:block;
            height:100%;
            width:auto;
            transition: all .3s;
        }
    }
    .right{
        margin-left:15px;
        width:227px;
    }
    .tit{
        padding:6px 0 10px 0;
    }
}
.r-agency-list{
    dd{
        width:970px;
        overflow:hidden;
    }
    .card{
        float:left;
        width:385px;
        padding:14px 0;
        margin-right:100px;
    }
    .tit{
        padding:12px 0 16px;
    }
    .row{
        font:12px/24px "Microsoft YaHei";
        margin-bottom:12px;
    }
    .left{
        float:left;
        width:85px;
        padding-left:30px;
        color:#999;
        background:url(../../image/search-icon-2.png) no-repeat left top;

        &.icon1{
            background-position:left -50px;
        }
        &.icon2{
            background-position:left -100px;
        }
        &.icon3{
            background-position:left -150px;
        }
    }
    .right{
        float:left;
        width:270px;
        color:#666;
    }
}
.r-type-guide-list{
    dd{
        width:970px;
        overflow:hidden;
    }
    .card{
        float:left;
        width:385px;
        padding:14px 0;
        margin-right:100px;
    }
    .tit{
        padding:12px 0 16px;
        .daq-icon{
            margin-left:12px;
            font-size:16px;
            color:#12a7ee;
            &.sex1{
                color:#f1327e;
            }
        }
    }
    .row{
        font:12px/24px "Microsoft YaHei";
        margin-bottom:6px;
    }
    .left{
        float:left;
        width:66px;
        color:#999;
    }
    .right{
        float:left;
        width:270px;
        color:#666;
    }
}
.r-pic-list{
    dd{
        width:892px;
        padding:30px 0;
        overflow:hidden;
    }
    .card{
        float:left;
        width:200px;
        margin:0 22px 22px 0;
        &:hover{
            img{
                transform: scale(1.2);
            }
        }
    }
    .top{
        position:relative;
        display: block;
        width:200px;
        height:150px;
        overflow:hidden;
        cursor:pointer;
        i{
            position:absolute;
            left:50%;
            top:50%;
            margin: -18px 0 0 -18px;
            width:36px;
            height:36px;
            background:url(../../image/g-sarch-play-video.png) no-repeat;
        }
        img{
            height:100%;
            display:block;
            margin: 0 auto;
            transition: all .3s ease-out;
        }
    }
    .tit{
        text-align:center;
        padding-top:14px;
        &.text-left{
            text-align: left;
        }
        .price{
            float:right;
            height:24px;
            line-height:24px;
            font-size:22px;
            font-family: 'Arial';
            color:$themeBuyColor;
            span{
                font-size:12px;
                font-family: "Microsoft Yahei";
            }

        }
    }
}






