html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, dd, dl, dt, li, ol, ul, input, select, button, textarea {
  padding: 0;
  margin: 0;
  border: none;
}
body,html{
  height:auto !important;
  overflow: visible !important;
  font-family: "Microsoft YaHei",SimSu,Arial,"Helvetica neue"!important;
}
input, button, select, textarea, a, img {
  outline: none;
}

::-moz-focus-inner {
  border: 0px;
}
/*  */
.header-top{
    background: #eee;
    height: 31px;
    line-height: 30px;
    color: #808080;
    border-bottom: 1px solid #eee;
    .top-left{
        span{
            font-size: 12px;
            color: #808080;
        }
        a{
            color:$themeColor;
            margin-left: 30px;
            &:hover{
                text-decoration: underline;
            }
        }
    }
    .top-right{
        .top-center{
            width:100px;
            text-align: center;
            cursor: pointer;
            display: none;
            .center-order{
                border-top: 0;
                position: relative;
                z-index: 20;
                i{
                    font-size: 12px;
                    color: #808080;
                    vertical-align: middle;
                }
            }
            .center-order:hover{
                border-color:#eee;
            }
            .center-order:hover .centerMenu{
                display: block;
                border-color: #eee;
            }
        }
        .centerMenu{
            background: #fff;
            display: none;
            position: absolute;
            width: 100px;
            border: 1px solid #fff;
            border-top: none;
            box-sizing: border-box;
            left: -1px;
            top:32px;
            z-index: 100;
            a{
                display: block;
            }
        }
        .centerMenu li:hover a{
            color: $themeColorHover;
        }
        .right-ewm{
            cursor: pointer;
            padding: 0 3px;
            position: relative;
            z-index: 2;
            border-left: 1px solid;
            border-right: 1px solid;
            border-bottom: 1px solid;
            border-color:#eee;
            img{
                vertical-align: middle;
                margin-top: -2px;
            }
            a{
                color: #808080;
            }
            .ewm{
                position: absolute;
                left: -1px;
                top: 32px;
                width: 100px;
                height: 100px;
                padding: 15px;
                border: 1px solid #eee;
                border-top: none;
                background: #fff;
                display: none;
                img{
                    height: 100%;
                }
            }
        }
        .right-ewm:hover{
            border-color:#eee;
            .ewm{
                display: block;
            }
            a{
                color: #3eb4fa;
            }
        }
        .cart-con{
            display:none;
            position:relative;
            margin-right:14px;
            padding:0 13px;
            border-left:1px solid #eee;
            border-right:1px solid #eee;
            cursor: pointer;
            &:hover{
                border-color:#dadada;
                background:#fff;
                .cart-down{
                    display: block;
                }
            }
            .daq-icon{
                margin-right:6px;
                color:#e40077;
            }
            em{
                margin-left:5px;
                font-style: normal;
                color:#e40077;
            }
            .cart-down{
                display: none;
                position:absolute;
                z-index:10;
                left:-211px;
                top:31px;
                background:#fff;
                width:307px;
                border:1px solid #dadada;
                border-top:none;
                max-height:442px;
                overflow: hidden;
                overflow-y:scroll;
                border-bottom-left-radius: 4px;
                border-bottom-right-radius: 4px;
                .cart-no-data{
                    padding:12px;
                    color:#bbb;
                    font-size:12px;
                    line-height:58px;
                    img{
                        margin-right:8px;
                        vertical-align: middle;
                    }
                }
                .cart-datas{
                    h3{
                        margin:0 9px;
                        padding:6px 5px 0;
                        border-bottom:1px solid #dadada;
                        font:12px/32px "Microsoft YaHei";
                        color:#666;
                    }
                    .c-l{
                        padding:15px 15px 0;
                    }
                    .c-l-l{
                        width:49px;
                        height:49px;
                        border:1px solid #dadada;
                        margin-right:9px;
                        img{
                            width:100%;
                            height:100%;
                            display: block;
                            margin:0 auto;
                        }
                    }
                    .c-l-c{
                        width:100px;
                        font:12px/16px "Microsoft YaHei";
                        color:#888;
                    }
                    .c-l-r{
                        width:85px;
                        font:12px/16px "Microsoft YaHei";
                        color:#888;
                        text-align:right;
                    }
                    .cc{
                        padding-bottom:15px;
                    }
                    .c-l-r-b{
                        padding-top:10px;
                        a{
                            color:#888;
                            &:hover{
                                text-decoration: underline;
                            }
                        }
                    }
                    .c-bottom{
                        padding:12px;
                        border-top:1px solid #dadada;
                        a{
                            display:block;
                            height:26px;
                            width:76px;
                            font:12px/26px "Microsoft YaHei";
                            color:#fff;
                            text-align: center;
                            border-radius: 3px;
                            background: #eb2668;
                        }
                        .red-font{
                            color: #eb2668;
                        }
                    }
                }
            }
        }
    }
}
/*  */
.header-middle{
    background: #fff;
    height: 102px;
    .logo{
        //height: 44px;
        margin-top: 19px;
    }
    .search{
        margin-top: 28px;
    }
    .search-input{
        width: 400px;
        height: 20px;
        border:2px solid $themeColor;
        padding: 5px 10px;
    }
    .search-btn{
        width:80px;
        height: 34px;
        border:2px solid $themeColor;
        background: $themeColor;
        text-align: center;
        line-height: 30px;
        color: #fff;
        font-size: 14px;
        cursor: pointer;
        &:hover{
            text-decoration: underline;
        }
    }
    .header-nav{
        font-size: 12px;
        color: #333;
        margin-top: 10px;
        margin-left:-24px;
        li{
            margin-left: 24px;
            &:nth-of-type(2n){
                a{
                    color:$themeColor;
                }
            }
            a:hover{
                text-decoration: underline;
            }
        }

    }
}
/*  */
.nav-hb{
    height: 50px;
    background:$themeColor;
    .head-nav{
        position: relative;
        .nav-li{
            float: left;
            width: 200px;
            line-height: 50px;
            height: 50px;
            font-size: 15px;
            color: #333;
            text-align: center;
            >a{
                color: #fff;
                display: block;
            }
        }
        .active{
            background:$themeColorHover;
            color: $themeColor;
            >a{
                color: #fff;
            }
        }
        .nav-li:hover{
            background:$themeColorHover;
            >a{
                color: #fff;
            }
            .sub-menu{
                display: block;
            }
        }
    }
}
