/**

 */
.current-position{
    height:40px;
    background-color: white;
    .center{
        font:12px/40px "Microsoft YaHei UI";
        color:#888;
        span{
            font:12px/40px "Microsoft YaHei UI";
            color:#888;
        }
        a{
            font:12px/40px "Microsoft YaHei UI";
            color:#888;
        }
