 */
@font-face {
    font-family: 'm-iconfont';  /* project id 657935 */
    src: url('//at.alicdn.com/t/font_657935_tpak8hvmqid.eot');
    src: url('//at.alicdn.com/t/font_657935_tpak8hvmqid.eot?#iefix') format('embedded-opentype'),
    url('//at.alicdn.com/t/font_657935_tpak8hvmqid.woff') format('woff'),
    url('//at.alicdn.com/t/font_657935_tpak8hvmqid.ttf') format('truetype'),
    url('//at.alicdn.com/t/font_657935_tpak8hvmqid.svg#m-iconfont') format('svg');
}
.daq-icon {
    font-family: "m-iconfont" !important;
    font-size: 16px;
    font-style: normal;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

