.register-header{
    background-color: white;
    width:100%;
    height:220px;
    .center{
        border-bottom: 1px solid #ddd;
        height:124px;
    }
    .logo{
        margin-top: 30px;
        float:left;
        display:block;
        width:468px;
        height:60px;
        background: url("../../image/logo.png") no-repeat 25px;
    }
    .pagetitle{
        margin-left: 36px;
        font:24px/123px "Microsoft YaHei UI";
    }
    .toregister{
        margin-right: 21px;
        float:right;
        font:14px/124px "Microsoft YaHei UI";
        color:#999;
        &:hover{
            text-decoration: underline;
        }
    }
    .tologin{
        float:right;
        margin-right: 24px;
        font:14px/124px "Microsoft YaHei UI";
        color:#999;
        &:hover{
            text-decoration: underline;
        }
    }
    .phone{
        float:right;
        font:14px/124px "Microsoft YaHei UI";
        color:$themeColor;
        i{
            margin-right: 9px;
            display:inline-block;
            width:1px;
            height:16px;
            vertical-align: middle;
            background-color: #ccc;
        }
    }
    .pagetype{
        position: relative;
        top:-1px;
        display:flex;
        justify-content: space-between;
        width:839px;
        margin: 0 auto;
        li{
            position: relative;
            float:left;
            height:70px;
            width:228px;
            text-align: center;
            font:14px/70px "Microsoft YaHei UI";
            &.curr{
                border-top: 2px solid $themeColor;
                color:$themeColor;
                i{
                    background-color: $themeColor;
                }
            }
            i{
                position: absolute;
                left:50%;
                margin-left: -14px;
                top:-14px;
                display:block;
                width:28px;
                height:28px;
                border-radius: 28px;
                font:14px/28px "Microsoft YaHei UI";
                color:white;
                background-color: #bbb;
            }
        }
    }
}
/* 404 */
.common-four{
    position: fixed;
    top:0;
    bottom: 0;
    width: 100%;
    background: #fff;
    .error, .error-bg{
        position: absolute;
    }
    .error{
        left: 50%;
        top: 40%;
        margin: -145px 0 0 -196px;
        width: 392px;
        height: 290px;
    }
    .error-hd{
        position: relative;
        overflow: hidden;
        width: 392px;
        height: 166px;
    }
    .error-false{
        position: absolute;
        left: 0;
        top: 0;
        z-index: 10;
        width: 100%;
        height: 100%;
        background: url(../../image/error404.png) no-repeat;
    }
    //500
    .error-service-hd{
        width: 379px;
    }
    .error-service{
        position: absolute;
        left: 0;
        top: 0;
        z-index: 10;
        width:379px;
        height: 100%;
        background: url(../../image/error500.png) no-repeat;
    }
    .error-banner{
        position: absolute;
        width: 1024px;
        height: 535px;
        top:0;
        left: 0;
        z-index: 1;
        background: url(../../image/error.jpg) no-repeat;
        animation: move 15s linear 0s infinite alternate
    }
    @keyframes move{
        0%   {top:0;left:0}
        100% {left:-630px;top:-369px}
    }
}
.error-bd{
    text-align: center;
    p{
        padding: 20px 0;
        font-size: 16px;
        color: #999;
    }
}
.error-ft{
    a{
        display: inline-block;
        width: 100px;
        height: 36px;
        margin: 0 10px;
        border-radius: 18px;
        line-height: 36px;
        font-size: 14px;
        color: $themeColor;
        border: 1px solid $themeColor;
    }
}
