@mixin mask-five{
  background:url(../image/mask-5.png) repeat;
}
//60
@mixin mask-six{
  background:url(../image/mask-6.png) repeat;
}
// 70
@mixin mask-seven{
  background:url(../image/mask-7.png) repeat;
}
//80
@mixin mask-eight{
  background:url(../image/mask-8.png) repeat;
}
//No Picture
@mixin no-photo{
  background:url(../image/no-photo.png) #eee center center no-repeat;
}
//No Picture
@mixin linkno-photo{
  background:url(../image/no-photo.png) #eee center center no-repeat;
  background-size: 80%;
}
//loadding
@mixin loading{
  background:url(../image/loading.gif) no-repeat;
  background-size:30px 30px;
}

@mixin ellipsis($w:overflow) {
  width: $w;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  word-wrap: normal;
}

// 
@mixin inline-block{
  display:inline-block;
  *display:inline;
  *zoom:1;
}
// 
@mixin pointer{
  cursor:pointer;
  cursor:hand;
}