@charset "UTF-8";
.submenu{
  width:180px;
  float:left;
  h2{
    text-align: left;
    background:$themeColor;
    color:#fff;
    font:16px/50px "Microsoft Yahei";
    padding: 0 14px;
  }
}
.submenu-list{
  &>li{
    background:#fff;
    border-bottom: 1px solid #e6e6e6;
    font:normal 14px/46px "Microsoft Yahei";
    overflow:hidden;
    &>a{
      display: block;
      color:#555;
      padding-left:13px;
      height:46px;
      line-height:46px;
    }
    &.curr>a{
      display: block;
      height:46px;
      line-height:46px;
      background:#f6fdff;
    }
    .icon{
      display: inline-block;
      zoom: 1;
      *display:inline;
      height:16px;
      width:16px;
      margin-right:9px;
      background:url(../../image/center1.png) no-repeat left;
      vertical-align: middle;
    }
    .icon1{
      background-position:0 -71px;
    }
    .icon2{
      background-position:-46px -71px;
    }
    .icon3{
      background-position:-72px -71px;
    }
    .icon4{
      background-position:-97px -71px;
    }
    .icon5{
      background-position:-122px -71px;
    }
  }
  .hassubmenu{
    position:relative;
    &>ul{
      display: none;
      margin-bottom:-1px;
    }

    &.curr>a{
      display: block;
      height:46px;
      line-height:46px;
      border-bottom: 1px solid #e6e6e6;
      background:#f6fdff;
    }
    .triangle{
      position:absolute;
      right:20px;
      top:21px;
      height:9px;
      width:5px;
      background:url(../../image/center1.png) no-repeat -170px -71px;
    }
    &.curr>.triangle{
      display:none;
    }
    &.curr>.triangle1{
      display: block;
      position:absolute;
      right:18px;
      top:23px;
      height:5px;
      width:9px;
      background:url(../../image/center1.png) no-repeat -147px -71px;
    }
    ul{
      background: #fff;
      li{
        padding:0 20px 0 40px;
        line-height: 34px;
        height: 34px;
        overflow: hidden;
        border-left: 2px solid #fff;
        border-top: 1px solid #fff;
        border-bottom: 1px solid #fff;
        &.curr,&:hover{
          background: #f3f8fa;
          border-left: 2px solid $themeColor;
          border-top: 1px solid #e7e7e7;
          border-bottom: 1px solid #e7e7e7;
          a{
            color:$themeColor;
          }
        }
      }
      .icon10{
        display: inline-block;
        zoom: 1;
        *display:inline;
        height:5px;
        width:5px;
        margin-right:7px;
        background:url(../../image/center1.png) no-repeat -274px -37px;
        vertical-align: middle;
      }
      a{
        color:#666;
        font-size:14px;
        font-weight: normal;
      }
    }
  }
}
