@charset "UTF-8";
.ui-toast{
    position: fixed;
    left:50%;
    top:50%;
    padding:0 12px;
    text-align:center;
    font:14px/26px "Microsoft YaHei";
    color:#fff;
    background:url(../image/mask-6.png) repeat;
    border-radius:3px;
    opacity:0;
    transition:all .25s ease 0s;
    transform:translate(-50%,-50%);
    z-index:999999;
    &.show{
        opacity: 1;
    }
    &.hide{
        opacity: 0;
    }
    .ui-toast-text{
        display: block;
    }
}
